.button {
  position: relative;
  margin: auto;
  padding: 0;

  width: 260px;
  height: 52px;

  background-color: transparent;
  border: 2px solid #444444;

  color: #444444;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;

  display: flex;
  align-items: center;
  justify-content: center;

  overflow: hidden;
  z-index: 1;
  transition: 0.4s;

  &:hover {
    color: #ffffff;
  }

  &:focus {
    color: #ffffff;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  &:hover::after {
    transform: translate(-50%) scale(4);
  }

  &:focus::after {
    transform: translate(-50%) scale(4);
  }

  &::after {
    content: "";

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%) scale(0);
    transform-origin: center;
    transition: 0.4s;

    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #444444;
    z-index: -1;
  }
}

@media (min-width: $tablet-width) {
  .button--scale:hover::after {
    transform: translate(-50%) scale(6);
  }

  .button--scal4:focus::after {
    transform: translate(-50%) scale(6);
  }
}
