.goods {
  padding: 34px 30px 42px;

  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: bottom;
  background-size: contain;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  &__title {
    margin: 0;
    margin-bottom: 30px;

    min-width: 100%;

    text-align: left;
    font-size: 25px;
    font-weight: 700;
    line-height: 24px;
  }

  &__week {
    color: #63d1bb;
    font-size: 17px;
    line-height: 20px;
    text-transform: uppercase;
  }

  &__wrapper {
    max-width: 320px;
  }

  &__description {
    margin-bottom: 24px;

    color: #444444;
    font-size: 15px;
    line-height: 24px;
  }

  &__params {
    margin-bottom: 42px;

    width: 100%;
    min-height: 92px;

    color: #444444;
    border-collapse: collapse;
  }

  &__params tr td {
    padding: 10px 0;
    width: 50%;
    border-bottom: 1px solid #cacaca;
  }

  &__price {
    margin: 29px 0;

    width: 100%;
    display: block;

    text-align: center;
  }
}

@media (min-width: $tablet-width) {
  .goods {
    position: relative;
    padding: 38px 50px 69px;

    flex-direction: column;

    &__title {
      margin-bottom: 48px;
      padding-right: 28px;
      font-size: 43px;
      line-height: 50px;

      display: flex;
      align-items: center;
      justify-content: space-between;
    }

    &__week {
      width: 70px;
      text-align: center;
    }

    &__wrapper {
      width: 100%;
      max-width: 100%;
    }

    &__description {
      margin-top: 0;
      margin-bottom: 40px;
      max-width: 300px;

      font-size: 17px;
      line-height: 30px;
    }

    &__params {
      margin: 0;
      max-width: 292px;
    }

    &__img {
      position: absolute;
      top: 148px;
      right: 50px;
    }

    &__price {
      margin-top: 68px;
      margin-bottom: 40px;

      max-width: 292px;

      text-align: center;
      font-size: 20px;
    }

    &__button {
      margin: 0;

      min-width: 292px;
    }
  }
}
