.header {
  min-width: 320px;

  &__menu {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__item {
    position: relative;

    min-width: 100%;
    min-height: 79px;

    display: flex;
    align-items: center;
    justify-content: center;

    font-size: 17px;
    font-weight: 700;

    &::before {
      content: "";
      position: absolute;
      left: -31px;
      bottom: 0;

      width: 120%;
      height: 1px;

      background: repeating-linear-gradient(90deg, #cacaca 3px, #cacaca 10px, transparent 10px, transparent 20px);
    }

    &:nth-child(5)::before {
      background: #f9e3fa;
      border-bottom: 1px solid #ececec;
    }

    &--subtitle {
      display: none;
    }
  }

  &__wrapper {
    min-width: 320px;
  }

  &__logo:hover {
    opacity: 0.6;
  }

  &__logo:focus {
    opacity: 0.6;
  }

  &__logo:active {
    opacity: 0.3;
  }

  &__link {
    position: relative;
    color: #000000;
    padding-left: 76px;

    width: 100%;

    display: flex;
    align-items: center;

    &--padding {
      min-height: 18px;
    }

    &:hover {
      color: rgba(0, 0, 0, 0.6);
    }

    &:focus {
      color: rgba(0, 0, 0, 0.6);
    }

    &:hover .header__img {
      fill: #63d1bb;
      opacity: 1;
    }

    &:focus .header__img {
      fill: #63d1bb;
      opacity: 1;
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }

    &:active .header__img {
      fill: #63d1bb;
      opacity: 0.3;
    }
  }

  &__img {
    position: absolute;
    top: 0;
    left: 26px;
    opacity: 0.2;
  }
}

@media (min-width: $tablet-width) {
  .header {
    padding: 0 50px;
    min-width: 768px;

    background: linear-gradient(#ffffff 87px, #cacaca 88px, #f9f9f9 88px, #f9f9f9 100%);

    &--bgcolor {
      background: linear-gradient(#ffffff 87px, #cacaca 88px, #f1f1f1 88px, #f1f1f1 100%);
    }

    &__menu {
      flex-direction: row;
      flex-wrap: wrap;
    }

    &__wrapper {
      min-width: 0;
    }

    &__item {
      min-width: 0;
      min-height: 88px;
      order: 2;

      font-size: 15px;

      &::before {
        display: none;
      }

      &:first-child {
        min-width: 320px;

        order: 1;
        justify-content: flex-start;
      }

      &:nth-child(2) {
        padding-bottom: 28px;
      }

      &:nth-child(3) {
        padding-left: 37px;
        padding-bottom: 28px;
      }

      &:nth-child(4) {
        order: 1;
        font-size: 0;

        min-width: 84px;
        border-left: 1px solid #ececec;
        border-right: 1px solid #ececec;
      }

      &:nth-child(5) {
        padding-left: 6px;
        order: 1;
        font-size: 17px;
      }

      &--subtitle {
        padding-left: 132px;
        padding-bottom: 28px;
        color: #444444;
        font-size: 15px;
        font-weight: 400;
        line-height: 24px;
      }
    }

    &__link {
      padding: 0;

      &--padding {
        padding-left: 76px;
      }
    }

    &__img--position {
      position: relative;
      left: 0;
    }
  }
}