.textfield {
  margin-top: 24px;
  display: block;
  font-size: 17px;
  font-weight: 700;
  line-height: 18px;

  &--margin {
    position: relative;
    margin-top: 34px;
  }

  &__input {
    padding: 0;
    min-width: 100%;
    height: 52px;

    border: none;
    border-bottom: 2px solid #000000;
    background-color: inherit;

    color: #444444;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: inherit;
    font-weight: 400;
    line-height: inherit;

    outline: none;

    &:hover {
      border-color: #62d1ba;
    }

    &:hover + .textfield__icon {
      fill: #62d1ba;
    }

    &:focus {
      color: #444444;
      border-color: #62d1ba;
    }

    &:focus + .textfield__icon {
      fill: #62d1ba;
    }

    &::placeholder {
      color: rgba(44, 44, 44, 0.3);
      font-family: inherit;
      font-size: inherit;
      font-weight: 400;
      line-height: inherit;
    }
  }

  &__icon {
    position: absolute;
    top: 64%;
    right: 6px;
    transform: translateY(-50%);
  }

  &__textarea {
    resize: none;

    margin-top: 24px;
    padding: 18px;
    width: 100%;
    height: 142px;
    border: 2px solid #000000;
  }
}

@media (min-width: $tablet-width) {
  .textfield {
    width: 100%;
    font-size: 19px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    &--margin {
      margin-top: 24px;
      padding: 0;
      font-size: 17px;
    }

    &--mt {
      margin-top: 0;
      align-items: flex-start;
    }

    &__input {
      margin-left: 26px;
      padding-left: 18px;
      flex-grow: 1;
      min-width: 0;

      &--width {
        max-width: 500px;
      }
    }

    &__textarea {
      margin-top: 0;
    }

    &__icon {
      top: 50%;
    }
  }
}
