.video {
  padding: 30px 30px 0;

  &__title {
    position: relative;
    margin: 0;
    padding: 29px 43px 26px 90px;

    border: 1px solid #ececec;
    border-bottom: none;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-size: 17px;
    line-height: 24px;

    &::before {
      content: "";
      position: absolute;
      left: 34px;

      width: 32px;
      height: 22px;
      background-image: url("../img/icon-video.svg");
      background-position: 50%;
    }
  }

  &__wrapper-play {
    position: relative;

    display: flex;
    justify-content: center;
  }

  &__play {
    position: absolute;
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);

    width: 101px;
    height: 77px;
    display: block;

    background-image: url("../img/icon-play.svg");

    transition: 0.1s;

    &:hover {
      transform: translate(-50%, -50%) scale(1.2);
    }

    &:focus {
      transform: translate(-50%, -50%) scale(1.2);
    }

    &:active {
      transform: translate(-50%, -50%) scale(0.8);
    }
  }

  &__wrapper {
    padding: 0 34px 33px;

    border: 1px solid #ececec;
    border-top: none;

    &:hover {
      box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    }

    &:focus {
      box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    }

    &:active {
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    }
  }

  &__description {
    margin: 0;
    padding: 34px 9px 30px 0;
    color: #444444;
    font-size: 13px;
    line-height: 24px;
  }

  &__button {
    width: 192px;
  }
}

@media (min-width: $tablet-width) {
  .video {
    padding: 52px 50px 0;
    display: flex;
    flex-direction: column;

    &__order {
      order: 2;

      &--top {
        order: 1;
      }
    }

    &__title {
      padding-left: 100px;
      padding-top: 40px;
      font-size: 20px;
      line-height: 24px;

      &::before {
        left: 42px;
      }
    }

    &__wrapper {
      padding-left: 40px;
      padding-right: 40px;
      padding-bottom: 46px;
    }

    &__description {
      padding-top: 16px;
      padding-bottom: 40px;
      padding-right: 0;
      font-size: 15px;
      line-height: 30px;
    }

    &__button {
      width: 100%;
    }
  }
}
