.features {
  padding: 34px 30px 30px;

  &__title {
    margin: 0;
    margin-bottom: 30px;

    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    margin-bottom: 32px;

    width: 100%;
    max-width: 320px;
    min-height: 92px;
    max-height: 92px;

    font-size: 17px;
    font-weight: 700;
    line-height: 20px;

    display: flex;
    align-items: flex-end;

    background-repeat: no-repeat;

    &:first-child {
      background-image: url("../img/icon-feature-flower.svg");
    }

    &:nth-child(2) {
      background-image: url("../img/icon-feature-money.svg");
    }

    &:last-child {
      background-image: url("../img/icon-feature-heart.svg");
    }

    &--second {
      &:first-child {
        background-image: url("../img/icon-feature-ball.svg");
      }

      &:nth-child(2) {
        background-image: url("../img/icon-feature-rocket.svg");
      }

      &:last-child {
        margin-bottom: 0;
        background-image: url("../img/icon-feature-gift.svg");
      }
    }
  }
}

@media (min-width: $tablet-width) {
  .features {
    padding: 50px 50px 102px;

    &__title {
      margin-bottom: 46px;
      font-size: 43px;
      line-height: 50px;
    }

    &__item {
      margin-bottom: 57px;

      font-size: 20px;
      line-height: 22px;

      &:last-child {
        margin: 0;
      }
    }
  }
}
