.contacts {
  padding: 40px 0;

  &__title {
    margin: 0;
    margin-bottom: 42px;
    padding: 0 30px;

    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
  }

  &__info {
    margin-bottom: 42px;
    padding: 0 30px;
    width: 100%;
    max-width: 400px;

    display: flex;

    color: #444444;
    font-size: 15px;
    line-height: 30px;
  }

  &__email {
    margin-left: 32px;
    color: #444444;

    border-bottom: 1px solid #6bc5b3;

    &:hover {
      color: #46c1ae;
    }

    &:focus {
      color: #46c1ae;
    }

    &:active {
      opacity: 0.3;
    }
  }

  &__adress {
    margin-left: 32px;
    color: #444444;
    max-width: 170px;
  }

  &__map-wrapper {
    position: relative;

    width: 100%;

    display: flex;
    justify-content: center;
  }

  &__button {
    margin-top: 40px;
  }
}

@media (min-width: $tablet-width) {
  .contacts {
    padding: 50px 0 55px;

    display: flex;
    flex-wrap: wrap;

    &__title {
      margin-bottom: 34px;
      padding: 0 46px;

      width: 100%;
      height: 81px;

      font-size: 43px;
      line-height: 50px;

      background-image: url("../img/icon-flag.svg");
      background-repeat: no-repeat;
      background-position: 93%;

      display: flex;
      align-items: center;
    }

    &__info {
      margin: 0 50px 60px;
      padding: 0;

      max-width: 300px;

      font-size: 17px;
      line-height: 30px;

      &--margin {
        margin-left: 0;
      }
    }

    &__email {
      margin-left: 26px;
      max-height: 28px;
    }

    &__adress {
      margin-left: 26px;
      max-width: 220px;
    }

    &__img {
      width: 760px;
    }

    &__button {
      margin-top: 55px;
      width: 230px;
    }
  }
}
