.index-header {
  min-height: 506px;

  background-image: url("../img/hero-mobile@1x.jpg");
  background-position: 50% 0;
  background-size: 575px 373px;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    margin-top: 76px;
    margin-bottom: 134px;

    max-width: 244px;

    text-align: center;
    font-size: 27px;
    font-weight: 700;
    line-height: 30px;
  }

  &__link {
    padding-right: 36px;
    padding-left: 30px;

    width: 100%;
    min-height: 103px;

    background-color: #63d1bb;

    color: #ffffff;
    font-size: 20px;
    font-weight: 700;
    line-height: 24px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    &:hover {
      color: rgba(255, 255, 255, 0.6);
    }

    &:focus {
      color: rgba(255, 255, 255, 0.6);
    }

    &:hover::after {
      opacity: 0.6;
    }

    &:focus::after {
      opacity: 0.6;
    }

    &:active {
      color: rgba(255, 255, 255, 0.3);
    }

    &:active::after {
      opacity: 0.3;
    }

    &::after {
      content: "";

      width: 40px;
      height: 62px;
      background-image: url("../img/interior.svg");
    }

    &--darker {
      padding-right: 27px;
      background-color: #56c3b2;
      margin-top: 1px;

      &::after {
        width: 60px;
        height: 60px;
        background-image: url("../img/toy.svg");
      }
    }
  }
}

@media (min-width: $tablet-width) {
  .index-header {
    position: relative;
    max-height: 666px;
    background-image: url("../img/hero-tablet@1x.jpg");
    background-size: 1150px 746px;
    background-position: 50% -88px;

    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    &__wave {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 24px;
      background-image: url("../img/bg-zigzag-fill.svg");
      background-size: cover;
      background-position: -22px 0;
    }

    &__title {
      margin-top: 66px;
      margin-bottom: 374px;
      max-width: 500px;

      font-size: 43px;
      line-height: 50px;
    }

    &__link {
      margin-bottom: 12px;
      padding-left: 36px;
      width: 334px;
      height: 120px;

      line-height: 30px;

      &:hover {
        min-height: 125px;
        transform: translateY(-5px);
      }

      &:focus {
        min-height: 125px;
        transform: translateY(-5px);
      }

      &--darker {
        margin-left: 1px;
      }
    }
  }
}
