.card {
  margin-top: 30px;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  &__info {
    margin-top: 1px;
    padding: 30px 28px;
    border: 1px solid #ececec;

    min-width: 260px;
    max-width: 260px;

    transition: 0.1s;

    &:hover {
      box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    }

    &:focus {
      box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    }

    &:active {
      box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
    }
  }

  &__title {
    margin: 0;
    font-size: 17px;
    line-height: 24px;

    &:hover {
      color: rgba(0, 0, 0, 0.6);
    }

    &:focus {
      color: rgba(0, 0, 0, 0.6);
    }

    &:active {
      color: rgba(0, 0, 0, 0.3);
    }
  }

  &__description {
    margin-top: 17px;
    margin-bottom: 27px;
    padding-bottom: 27px;

    border-bottom: 1px solid #cacaca;

    color: #444444;
    font-size: 13px;
  }

  &__price {
    width: 100%;

    display: flex;
    justify-content: space-between;

    font-size: 15px;
  }

  &__cart {
    padding: 0;
    font-size: 0;
    border: none;

    background-color: transparent;
  }

  &__icon {
    opacity: 0.2;
    cursor: pointer;

    &:hover {
      fill: #63d1bb;
      opacity: 1;
    }

    &:focus {
      fill: #63d1bb;
      opacity: 1;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

@media (min-width: $tablet-width) {
  .card {
    margin-top: 54px;

    &__info {
      min-width: 334px;
      min-height: 332px;
    }

    &__title {
      padding-right: 40px;
      font-size: 20px;
      line-height: 24px;
    }

    &__description {
      padding-bottom: 120px;
      font-size: 15px;
      line-height: 30px;
    }

    &__price {
      font-size: 17px;
      line-height: 24px;
    }
  }
}
