.modal {
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);

  min-width: 100vw;
  max-width: 768px;
  height: 120vh;

  background-color: rgba(255, 255, 255, 0.9);

  z-index: 999;

  display: none;
  align-items: center;
  justify-content: center;

  &__wrapper {
    padding: 36px 30px 30px;

    border: 1px solid #c6c6c6;
    background-color: #ffffff;
    box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);

    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin-top: 0;

    max-width: 150px;

    text-align: center;
    font-size: 25px;
    line-height: 30px;
    text-transform: uppercase;
  }

  &__description {
    margin: 0;

    color: #444444;
    font-size: 15px;
    line-height: 30px;
  }

  &__form {
    min-width: 100%;
  }

  &__list {
    margin: 30px 0;
    min-width: 100%;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__size {
    width: 82px;
    height: 52px;

    background-color: transparent;
    border: 2px solid rgba(44, 44, 44, 0.1);

    color: #444444;
    font-size: 17px;
    font-weight: 700;

    outline: none;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__input:hover {
    border: 2px solid rgba(44, 44, 44, 0.3);
  }

  &__input:focus + &__size {
    border: 2px solid rgba(44, 44, 44, 0.3);
  }

  &__input:checked + &__size {
    border: 2px solid #62d1ba;
  }

  &__button {
    border: 2px solid #63d1bb;

    &::after {
      background-color: #63d1bb;
    }
  }
}

.modal__size--active {
  border-color: #62d1ba;
}

@media (min-width: $tablet-width) {
  .modal__wrapper {
    padding: 50px 105px 46px;
    width: 566px;
  }

  .modal__title {
    margin-bottom: 32px;
    max-width: 100%;

    font-size: 30px;
    line-height: 30px;
  }

  .modal__description {
    font-size: 17px;
    line-height: 30px;
  }

  .modal__list {
    margin-top: 26px;
  }

  .modal__size {
    min-width: 112px;
  }

  .modal__button {
    min-width: 100%;
  }
}
