.footer {
  padding: 25px 69px;

  background-color: #f1f1f1;

  &__logo {
    display: none;
  }

  &__list {
    padding: 0 20px;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__link:hover &__img {
    fill: #46c1ae;
  }

  &__link:focus &__img {
    fill: #46c1ae;
  }

  &__link:active &__img {
    opacity: 0.3;
  }

  &__copy {
    margin-top: 50px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    color: #000000;
    font-size: 19px;
    font-weight: 700;
    line-height: 18px;
  }

  &__copy:hover &__img {
    fill: #46c1ae;
  }

  &__copy:focus &__img {
    fill: #46c1ae;
  }

  &__copy:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .footer {
    padding: 56px 50px 40px;

    display: flex;
    justify-content: space-between;

    &__logo {
      padding-right: 118px;
      min-width: 220px;
      display: block;

      &:hover {
        fill: #46c1ae;
      }

      &:focus {
        fill: #46c1ae;
      }

      &:active {
        opacity: 0.3;
      }
    }

    &__list {
      padding: 0 40px;
      min-width: 220px;
    }

    &__copy {
      margin: 0;
      margin-right: -6px;
      padding-left: 33px;
      min-width: 220px;
    }

    &__img {
      margin-left: 20px;
    }
  }
}
