.check {
  position: relative;
  margin-top: 24px;
  padding-left: 62px;

  min-height: 40px;

  display: flex;
  align-items: center;

  font-size: 17px;
  font-weight: 700;
  line-height: 18px;

  &__input {
    appearance: none;
  }

  &__box {
    position: absolute;
    left: 0;
    margin: 0;

    width: 40px;
    height: 40px;
    border: 2px solid #444444;
    background-color: #ffffff;

    &:hover {
      border-color: #62d1ba;
    }

    &:active {
      border-color: rgba(44, 44, 44, 0.3);
    }
  }

  &__input:focus + &__box {
    border-color: #62d1ba;
  }

  &__input:checked + &__box::before {
    content: "";
    position: absolute;
    left: 7px;
    top: 50%;
    transform: translateY(-50%);

    width: 22px;
    height: 16px;

    background-image: url("../img/icon-tick.svg");
    background-repeat: no-repeat;
    background-position: 40% 96%;
  }

  &__box--radius {
    padding: 0;
    border-radius: 50%;
  }

  &__input:checked + &__box--radius::before {
    content: "";
    position: absolute;
    left: 10px;
    top: 50%;
    transform: translateY(-50%);

    width: 16px;
    height: 16px;

    border-radius: 50%;
    background: #62d1ba;
  }
}

@media (min-width: $tablet-width) {
  .check {
    font-size: 19px;

    min-height: 52px;

    &__box {
      width: 52px;
      height: 52px;
    }

    &--width {
      min-width: 200px;
    }

    &__input:checked + &__box::before {
      left: 0;
      width: 50px;
      height: 100%;
      background-position: 50%;
    }

    &__input:checked + &__box--radius::before {
      left: 13px;
      width: 22px;
      height: 22px;
    }
  }
}
