.main {
  padding-bottom: 55px;

  &__title {
    margin: 0;
    padding: 75px 36px;

    width: 100%;

    font-size: 27px;
    line-height: 30px;

    background-color: #f1f1f1;
  }

  &__description {
    margin: 30px 0;
    padding: 0 30px;
    color: #444444;
    font-size: 13px;
    line-height: 24px;
  }

  &__wrapper {
    padding: 0 30px;
  }
}

@media (min-width: $tablet-width) {
  .main {
    padding-bottom: 62px;

    &--padding {
      padding: 0;
    }

    &__title {
      padding: 8px 36px 60px;
      margin-bottom: 12px;
      font-size: 43px;
      line-height: 50px;
      text-align: center;

      background-image: url("../img/bg-zigzag-fill.svg");
      background-repeat: no-repeat;
      background-position: left 100.6%;
      background-size: 770px 24px;

      &--form {
        padding-bottom: 100px;
      }
    }

    &__wrapper {
      padding: 0 50px;
    }

    &__description {
      margin: 64px 0 44px;
      padding: 0 50px;
      font-size: 17px;
      line-height: 30px;
    }
  }
}
