.form {
  padding: 0 30px;

  &__field {
    display: flex;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    color: #63d1bb;
    text-transform: uppercase;

    font-size: 17px;

    &--margin {
      margin-top: 34px;
    }
  }

  &__placeholder {
    margin-top: 18px;
    margin-bottom: 30px;

    line-height: 16px;
  }

  &__submit {
    padding: 0;
  }

  &__line {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 8px;
      right: 0;

      width: 76.2%;
      height: 1px;
      background-color: #63d1bb;
    }

    &--cut::before {
      width: 66.2%;
    }
  }
}

@media (min-width: $tablet-width) {
  .form {
    padding: 0;

    &__field {
      padding: 0 50px;
      margin-bottom: 50px;
      flex-direction: row;
      flex-wrap: wrap;

      &--grey {
        padding-top: 26px;
        padding-bottom: 50px;
        background-color: #f1f1f1;
      }

      &--submit {
        align-items: center;
        justify-content: space-between;
      }
    }

    &__title {
      margin: 0;
      padding-top: 42px;
      min-width: 130px;
    }

    &__line::before {
      display: none;
    }

    &__wrapper {
      width: calc(100% - 130px);
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      &--width {
        width: 100%;
      }
    }

    &__placeholder {
      margin: 0;
      order: 2;
    }

    &__submit {
      margin: 0;
    }
  }
}
