.review {
  padding: 30px 30px 45px;

  background-image: url("../img/bg-zigzag-line.svg");
  background-repeat: repeat-x;
  background-position: bottom;
  background-size: contain;

  &__title {
    margin-bottom: 38px;

    font-size: 25px;
    font-weight: 700;
    line-height: 30px;
  }

  &__text {
    margin-top: 0;
    margin-bottom: 40px;

    color: #444444;
    font-size: 15px;
    line-height: 30px;
  }

  &__name {
    margin-bottom: 16px;

    display: inline-block;

    font-size: 17px;
    font-weight: 700;
  }

  &__profile {
    color: #444444;
    font-size: 17px;
  }

  &__wrapper {
    position: relative;
    margin: 37px 0;

    width: 100%;
    border: 1px solid #cacaca;

    display: flex;
    justify-content: center;

    &::before {
      content: "";

      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);

      width: 1px;
      height: 100%;
      background-color: #cacaca;
    }
  }

  &__arrow {
    padding: 0;

    width: 100%;
    height: 52px;

    border: none;
    background-color: transparent;
    background-image: url("../img/icon-left-arrow.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;

    font-size: 0;
    outline: none;

    &:hover {
      opacity: 0.6;
    }

    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }

    &--right {
      background-image: url("../img/icon-right-arrow.svg");
    }
  }
}

@media (min-width: $tablet-width) {
  .review {
    position: relative;
    padding: 80px 50px 136px;

    background-image: url("../img/review@1x.jpg"), url("../img/bg-zigzag-line.svg");
    background-repeat: no-repeat, repeat-x;
    background-position: left top, left bottom;
    background-size: 350px 460px, contain;

    &__title {
      margin-top: 0;
      margin-bottom: 48px;
      font-size: 43px;
      line-height: 50px;
    }

    &__block {
      min-height: 220px;
      max-width: 671px;

      display: flex;
      flex-direction: column;
    }

    &__text {
      margin-bottom: 62px;
      font-size: 17px;
    }

    &__wrapper {
      position: absolute;
      bottom: 120px;
      right: 69px;

      border: none;

      width: 137px;
      height: 26px;

      &::before {
        display: none;
      }
    }

    &__arrow {
      background-position: 0 50%;

      outline: none;

      &--right {
        background-position: 100% 50%;
      }
    }

    &__button {
      position: absolute;
      top: 80px;
      right: 50px;

      width: 175px;
    }
  }
}
